
import {catchError, mergeMap,  map } from 'rxjs/operators';
import { EditorNewsService } from './../services/editor-news.service';
import { Injectable } from '@angular/core';
import { Observable ,  of } from 'rxjs';
import { Action } from '@ngrx/store';




import {Actions, Effect, ofType} from '@ngrx/effects';

import * as NewsActions from './news-editor.actions';
import { News } from '../../news/models/news';

@Injectable()
export class NewsEditorEffects {

  constructor(
    private actions$: Actions,
    private newsService: EditorNewsService,
  ) { }

  @Effect()
  GetMyNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetReviewNews>(NewsActions.GET_REVIEW_NEWS),
      mergeMap(action =>
        this.newsService.getReviewNews().pipe(
        map((data) => {
          console.log(data);
          return new NewsActions.GetReviewNewsSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetReviewNewsError())),)
      )
    );

  @Effect()
  ApproveNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.ApproveNews>(NewsActions.APPROVE_NEWS),
      mergeMap(action =>
        this.newsService.approveNews(action.payload.id).pipe(
        map((data) => {
          console.log(data);
          return new NewsActions.ApproveNewsSuccess({ id: action.payload.id });
        }),
        catchError(() => of(new NewsActions.ApproveNewsError())),)
      )
    );

  @Effect()
  RejectNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.RejectNews>(NewsActions.REJECT_NEWS),
      mergeMap(action =>
        this.newsService.reJectNews(action.payload).pipe(
        map((data) => {
          console.log(data);
          return new NewsActions.RejectNewsSuccess(action.payload);
        }),
        catchError(() => of(new NewsActions.RejectNewsError())),)
      )
    );

  // @Effect()
  // GetTrashNews$: Observable<Action> = this.actions$.
  //   ofType<NewsActions.GetReviewNews>(NewsActions.GET_REVIEW_NEWS)
  //   .mergeMap(action =>
  //     this.newsService.trashNews(action.payload.perPage, action.payload.currentPage)
  //     .map((data) => {
  //       return new NewsActions.GetReviewNewsSuccess(data);
  //     })
  //     .catch(() => of(new NewsActions.GetReviewNewsError()))
  //   );

  @Effect()
  getLists$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetLists>(NewsActions.GET_LISTS),
      mergeMap(action =>
        this.newsService.getNewsLists().pipe(
        map((data) => {
          console.log(data);
          // new NewsActions.GetSelectedList(); //load selected list
          return new NewsActions.GetListsSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetListsError())),)
      )
    );


  @Effect()
  getListsByDate$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetListsByDate>(NewsActions.GET_LISTS_BY_DATE),
      mergeMap(action =>
        this.newsService.getNewsListsByDate(action.payload.date).pipe(
        map((data) => {
          console.log(data);
          // new NewsActions.GetSelectedList(); //load selected list
          return new NewsActions.GetListsByDateSuccess(data);
        }),
        catchError(() => of(new NewsActions.GetListsByDateError())),)
      )
    );


  @Effect()
  AddToList$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.AddToList>(NewsActions.ADD_TO_LIST),
      mergeMap(action =>
        this.newsService.addNewsToList({ list_id: action.payload.list_id, news_id: action.payload.news_id }).pipe(
        map((data) => {
          console.log(data);
          return new NewsActions.AddToListSuccess(data);
        }),
        catchError(() => of(new NewsActions.AddToListError())),)
      )
    );


  @Effect()
  RemoveFromList$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.RemoveFromList>(NewsActions.REMOVE_FROM_LIST),
      mergeMap(action =>
        this.newsService.removeNewsFromList(action.payload.id).pipe(
        map((data) => {
          //console.log(data);
          return new NewsActions.RemoveFromListSuccess(data);
        }),
        catchError(() => of(new NewsActions.RemoveFromListError())),)
      )
    );


  @Effect()
  UpdateListOrder$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.UpdateListOrder>(NewsActions.UPDATE_LIST_ORDER),
      mergeMap(action =>
        this.newsService.orderList(action.payload).pipe(
        map((data) => {
          //console.log(data);
          return new NewsActions.UpdateListOrderSuccess(data);
        }),
        catchError(() => of(new NewsActions.UpdateListOrderError())),)
      )
    );


  @Effect()
  GetSelectedList$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.GetSelectedList>(NewsActions.GET_SELECTED_LIST),
      mergeMap(action =>
        this.newsService.getSelectedList().pipe(
        map((data) => {
          //console.log(data);
          return new NewsActions.GetSelectedListSuccess({ list_id: data });
        }),
        catchError(() => of(new NewsActions.GetSelectedListError())),)
      )
    );

  @Effect()
  UpdateSelectedList$: Observable<Action> = this.actions$.
    pipe(
      ofType<NewsActions.UpdateSelectedList>(NewsActions.UPDATE_SELECTED_LIST),
      mergeMap(action =>
        this.newsService.updateSelectedList(action.payload.list_id).pipe(
        map((data) => {
          //console.log(data);
          return new NewsActions.UpdateSelectedListSuccess({ list_id: data });
        }),
        catchError(() => of(new NewsActions.UpdateSelectedListError())),)
      )
    );

}
