import { Component, OnInit, Inject, EventEmitter } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { News } from './../../news/models/news';

@Component({
  selector: 'app-notes-dialog',
  templateUrl: './notes-dialog.component.html'
})
export class NotesDialogComponent implements OnInit {
  onReject = new EventEmitter();
  noteInp;
  editorNotes;

  constructor(
    public dialogRef: MatDialogRef<NotesDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit() {
    this.editorNotes = new RejectNotes(this.data.id);
    console.log(this.data);
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

  addNote() {
    const tmpNote = this.noteInp;
    this.editorNotes.addNote(tmpNote);
    this.noteInp = '';
  }

  doReject() {
    this.closeDialog(false);
    this.onReject.emit(this.editorNotes);
  }

}

export class RejectNotes {

  news_id: any;
  notes?: any = [];
  containNotes = false;

  constructor(
    newsId
  ) {
    this.news_id = newsId;
  }

  addNote(note: string) {
    this.containNotes = true;
    this.notes.push(note);
  }
}
