import { Component, OnInit, Input, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { EditorNewsService } from '../services/editor-news.service';
import { News, ListNews, NewsList } from '../../news/models/news';
import { DataSource } from '@angular/cdk/table';
import { Observable } from 'rxjs';

// import { of } from 'rxjs';
import 'rxjs/add/observable/of';

import { Store } from '@ngrx/store';
import * as editorActions from '../store/news-editor.actions';
import * as newsActions from '../../news/store/news.actions';

import {Actions, ofType} from '@ngrx/effects';

@Component({
  selector: 'app-add-to-list-dialog',
  templateUrl: './add-to-list-dialog.component.html',
  styleUrls: ['./add-to-list-dialog.component.css']
})
export class AddToListDialogComponent implements OnInit {
  onDelete = new EventEmitter();
  onAdd = new EventEmitter();
  onUndo = new EventEmitter();

  newsData: News[];

  // mat table
  displayedColumns = ['news_name', 'heading', 'date', 'actions'];
  displayedColumnsBreaker = ['heading_br', 'actions_br'];
  dataSource: DataSource<any>;
  dataSource1: DataSource<any>;
  dataSource2: DataSource<any>;
  dataSource3: DataSource<any>;
  dataSource4: DataSource<any>;
  dataSource5: DataSource<any>;
  dataSource6: DataSource<any>;
  dataSource7: DataSource<any>;
  dataSourceBreakers: DataSource<any>;

  constructor(
    public dialogRef: MatDialogRef<AddToListDialogComponent>,
    public apiService: EditorNewsService,
    private store: Store<any>,
    private updates$: Actions,
    @Inject(MAT_DIALOG_DATA) public data: { list: NewsList }
  ) { }

  ngOnInit() {
    // console.log(this.data)
    this.apiService.getTodaysNews().subscribe((news: any) => {
      this.newsData = news;
      this.loadDataSource(this.newsData);
    });
  }

  loadDataSource(news_data: News[]) {
    this.dataSource  = new NewsListDataSource(this.checkIfInList(news_data["1"]["data"]));
    this.dataSource1 = new NewsListDataSource(this.checkIfInList(news_data["2"]["data"]));
    this.dataSource2 = new NewsListDataSource(this.checkIfInList(news_data["3"]["data"]));
    this.dataSource3 = new NewsListDataSource(this.checkIfInList(news_data["4"]["data"]));
    this.dataSource4 = new NewsListDataSource(this.checkIfInList(news_data["5"]["data"]));
    this.dataSource5 = new NewsListDataSource(this.checkIfInList(news_data["6"]["data"]));
    this.dataSource6 = new NewsListDataSource(this.checkIfInList(news_data["7"]["data"]));
    this.dataSourceBreakers = new NewsListDataSource(this.checkIfInList(news_data["breakers"]));
  }

  addToList(id: number) {
    console.log(id);
    // this.store.dispatch(new newsActions.UpdateMyNewsLoaded({ isMyNewsLoaded: false }));
    this.store.dispatch(new editorActions.AddToList({ list_id: this.data.list.id, news_id: id }));
    this.onAdd.emit({ list_id: this.data.list.id, news_id: id });


    this.updates$
    .pipe(ofType(editorActions.ADD_TO_LIST_SUCCESS))
    .subscribe(() => {
      this.store.select(news_store => news_store.ReviewNews.lists)
      .subscribe(list => {
        list.forEach((element: NewsList) => {
          if (element.id === this.data.list.id) {
            // console.log(element)
            this.data = { list: element };
            this.loadDataSource(this.newsData);
          }
        });
      });

    });
  }

  checkIfInList(news_data: News[]) {
    let news_re: News[] = [];
    let i_index = 0;
    news_data.forEach((news, index) => {
      let found = false;

      this.data.list.list_news.forEach((list_news, list_index) => {
        if (news.id === list_news.news.id) {
          found = true;
        }
      });

      if(!found){
        news_re[i_index] = news;
        i_index = i_index + 1;
      }

    });
    return news_re;
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

  doDelete(id) {
    this.closeDialog(false);
    this.onDelete.emit(id);
  }

  doAdd(id) {
    this.closeDialog(false);
    this.onAdd.emit(id);
  }

  doUndo(id) {
    this.closeDialog(false);
    this.onUndo.emit(id);
  }

}

export class NewsListDataSource extends DataSource<News> {
  constructor(private staticData: News[]) {
    super();
  }

  connect(): Observable<News[]> {
    return Observable.of(this.staticData);
  }

  disconnect() {}
}
