import { Router } from '@angular/router';
import { ConfirmDialogComponent } from './../../shared/component/confirm-dialog/confirm-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSidenav } from '@angular/material/sidenav';
import { Component, OnInit, ViewChild } from '@angular/core';

import { Store } from '@ngrx/store';
import { News } from './../../news/models/news';
import * as editorActions from '../store/news-editor.actions';

import { NotesDialogComponent, RejectNotes } from './../notes-dialog/notes-dialog.component';



@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.css']
})
export class ReviewComponent implements OnInit {
  them;
  user;
  sidePanelOpened = true;
  sidePanelOpened_noNews = true;
  review_news_count = null;

  @ViewChild('sidebar', {static: true}) sidenav: MatSidenav;

  // MESSAGE
  selectedNews: News;
  reviewList;

  // initializers
  init_news$;

  constructor(
    private store: Store<any>,
    private dialog: MatDialog,
    private router: Router
  ) {
    // this.selectedMessage = this.messages[1];
  }

  ngOnInit() {
    // this.init_news$ = this.store.select(store => store.ReviewNews.isLoaded)
    // .take(1)
    // .subscribe(
    //   (isLoaded) => {
    //     if (!isLoaded) {
    //       console.log('loading news for review');
    //       this.store.dispatch(new editorActions.GetReviewNews());
    //     }
    //   }
    // );
    this.store.dispatch(new editorActions.GetReviewNews());

    // no of news for approval count
    this.init_news$ = this.store.select(store => store.ReviewNews)
    .subscribe(
      (ReviewNews) => {
        if (ReviewNews.isLoaded) {
          this.review_news_count = Object.keys(ReviewNews.news).length;
          if (this.review_news_count <= 0) {
            this.sideBarToggle();
          }
        }
      }
    );

    this.reviewList = this.store.select(news_store => news_store.ReviewNews.news);
  }

  sideBarToggle() {
    this.sidenav.toggle();
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

  onSelect(news: News): void {
    if (this.selectedNews && this.selectedNews.id === news.id) {
      this.selectedNews = null;
    }else {
      this.selectedNews = news;
    }
  }

  approveNews(toApprove: News) {
    this.store.dispatch(new editorActions.ApproveNews({ id: toApprove.id }));
    this.selectedNews = null;
  }

  rejectNews() {

    let dialogRef = this.dialog.open(NotesDialogComponent, { width: '50%', data: this.selectedNews });

    dialogRef.afterClosed().subscribe((result: string) => {
      dialogRef = null;
      if (result) {
      }
    });
    dialogRef.componentInstance.onReject.subscribe((result: RejectNotes) => {
      this.store.dispatch(new editorActions.RejectNews(result));
      this.selectedNews = null;
    });

    return false;
  }

  onEdit() {
    if (this.selectedNews) {
      const url = '/editor/edit/' + this.selectedNews.id;
      this.router.navigateByUrl(url, { skipLocationChange: true });
    }
  }

}
