import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { environment } from './../../../environments/environment';
import { News, ListOrder } from '../../news/models/news';
import { RejectNotes } from '../notes-dialog/notes-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class EditorNewsService {
  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }

  // get all news in ready status (review)
  getReviewNews () {
    return this.http.get<any>(this.apiUrl + 'news/editor/ready');
  }

  // get all news in ready status (review)
  approveNews (id: number) {
    return this.http.post<any>(this.apiUrl + 'news/editor/approve', { newsId: id });
  }

  // reject news
  reJectNews (reject: RejectNotes) {
    return this.http.post<any>(this.apiUrl + 'news/editor/reject', reject);
    // { news_id: reject.news_id, notes: reject.notes, containNotes: reject.containNotes }
  }

  // get a news for editing
  editNews (id: number) {
    return this.http.get<any>( this.apiUrl + 'news/editor/edit/' + id );
  }

  // save a news, with bar(s) & ticker(s)
  updateNews (save_news: News, id: number) {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});

    save_news.newsstatus_id = 1;
    const post_str = JSON.parse(JSON.stringify(save_news));

    return this.http.put<any>(this.apiUrl + 'news/editor/' + id, post_str, { headers: headers });
  }


  /**
   * News List Services
   */

  // get todays lists
  getNewsLists () {
    return this.http.get<any>(this.apiUrl + 'news/editor/lists');
  }

  // get lists for given date, date format = 'Y-m-d' eg: 2018-11-20
  getNewsListsByDate (date: string) {
    return this.http.get<any>(this.apiUrl + 'news/editor/lists/' + date);
  }

  // get all news written today if published
  getTodaysNews () {
    return this.http.get<any>(this.apiUrl + 'news/editor/news/todays');
  }

  // add a news to a list
  addNewsToList (data: { list_id: number, news_id: number }) {
    return this.http.post<any>(this.apiUrl + 'news/editor/list/news/add', data);
  }

  // add a news to a list
  removeNewsFromList (news_id: number) {
    return this.http.delete<any>(this.apiUrl + 'news/editor/list/news/remove/' + news_id);
  }

  //order news in a list
  orderList(ordered_list: ListOrder[]) {
    return this.http.post<any>(this.apiUrl + 'news/editor/list/order/update', ordered_list);
  }

  //update selected news list
  updateSelectedList(list_id: number) {
    return this.http.put<any>(this.apiUrl + 'news/editor/list/selected/update', { "settings_value": list_id });
  }

  // get selected list
  getSelectedList () {
    return this.http.get<any>(this.apiUrl + 'news/lists/selected-list');
  }
}
